import {
  mapMutations,
  mapGetters
} from 'vuex';

export default {
  layout: 'admin',
  components: {

  },
  data() {
    return {
      table: {
        max: 10
      },
      page: 1,
      titleDialog: 'Tambah Informasi',
      tambahDialog: false,
      search: '',
      isUpdate: false,
      btnLoader: false,
      form: {
        judul: '',
        deskripsi: '',
        aktif: true
      }
    }
  },
  mounted() {
    this.$store.dispatch('informasi/getAll', {});
  },
  methods: {
    searchData(){
      this.$store.dispatch('informasi/getAll', {
        search: this.search
      });
    },
    edit(data) {
      this.form = JSON.parse(JSON.stringify(data))
      this.tambahDialog = true
      this.titleDialog = 'Edit Informasi'
      this.isUpdate = true
    },
    resetForm(){ 
      this.form ={
        judul: '',
        deskripsi: '',
        aktif: true
      },
      this.isUpdate = false
    },
    handleCurrentChange(val) {
      this.$store.commit('informasi/setPage', val)
      this.$store.dispatch('informasi/getAll', {});
    },
    onSubmit(type = 'store') {
      this.btnLoader = true
      let url = "/information/store";
      if (type == 'update') {
        url = `/information/${this.form.id}/update`
      }

      this.$axios.post(url, this.form).then(resp => {
        if (resp.data.success) {
          this.$notify.success({
            title: 'Success',
            message: `Berhasil ${type == 'store' ? 'Menambah' : 'Mengubah'} Informasi`
          })
          this.tambahDialog = false
          this.$store.dispatch('informasi/getAll', {});
        }
      }).finally(() => {
        this.btnLoader = false
      }).catch(err => {
        let error = err.response.data.data
        if (error) {
          this.showErrorField(error)
        } else {
          this.$notify.error({
            title: 'Error',
            message: err.response.data.message
          })
        }
      })
    },
    deleteInformasi(id) {
      this.$swal({
        title: 'Perhatian!',
        text: "Apakah anda yakin ingin menghapus data ini?",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Ya',
        cancelButtonText: 'Batal'
      }).then((result) => {
        if (result.isConfirmed) {
          this.$axios.delete(`/information/${id}/delete`).then(resp => {
            if (resp.data.success) {
              this.$notify.success({
                title: 'Success',
                message: 'Berhasil Menghapus Data'
              })
              this.tambahDialog = false
              this.$store.dispatch('informasi/getAll', {defaultPage:true});
            }
          }).finally(() => {
            //
          }).catch(err => {
            this.$notify.error({
              title: 'Error',
              message: err.response.data.message
            })
          })
        }
      })
    },
  },
  computed: {
    ...mapGetters("informasi", [
      'getInformations',
      'getLoader'
    ]),
  },
  watch: {
    getInformations(newValue, oldValue) {

    },
    search(newValue, oldValue) {
      // this.$store.dispatch('informasi/getAll', {
      //   search: newValue
      // });
    },
    page(newValue, oldValue) {
      this.$store.commit('berita/setPage', newValue)
      this.$store.dispatch('berita/getAll', {});
    }
  }
}

